import React, { useEffect, useRef, useState } from 'react'
import { useSearchParams } from 'react-router-dom'
import Confirmation from './Confirmation/Confirmation'
import NumberIndicator from './numberIndicator'
import SubmitButton from './Submit/Submit'




export default function Question({title, q:qe}) {

    // are sure to submut handler
    const [areYouSure, setAreYouSure] = useState(false);
    const areYouSureHandler = ()=> setAreYouSure(true);
    const areYouSureHandlerExit = ()=> setAreYouSure(!true)

    
    
    let [optionSelected, setOptionSelected] = useState(localStorage.getItem('questionID') || false)
    let rememberQuestion = new URLSearchParams(window.location.search).get('num')
    let [currentQ, setCurrentQ] = useState(+rememberQuestion || 0)
    let qesRef = useRef(null)
    let [searchParams, setSearchParams] = useSearchParams(window.location.search);
    let [answer,setAnwser] = useState()


    const nextQ = () => {
        if((currentQ+1) <= (qe.length-1)){
            setCurrentQ(currentQ+1)
            setSearchParams({ num:[currentQ+1] })
            }
    }
    const prevQ = () => {
         if(currentQ>0){
             setCurrentQ(currentQ-1)
             setSearchParams({ num:[currentQ+1] })
            
         }
    }


    useEffect(()=> {
        currentQ === 0 && setSearchParams({ num:[currentQ+1] })
        currentQ === 0 && setSearchParams({ num:[currentQ+1] })
        if(qe[currentQ]){
            !localStorage.getItem(title) && localStorage.setItem(title, JSON.stringify({}))
            if(JSON.parse(localStorage.getItem(title))){
                let answeredOptionInDB = JSON.parse(localStorage.getItem(title))[currentQ]
                if(answeredOptionInDB){
                    console.log(answeredOptionInDB);
                    //check the radio btn 
                    document.getElementById(answeredOptionInDB?.userAns)?.setAttribute('checked', true)
                }
                else{
                    document.querySelectorAll('input[type=radio]').forEach((v)=>{
                        v?.setAttribute('checked',false)
                    })
                }
            }
            qesRef.current.innerHTML = `${qe[currentQ].q.split('BOLD').join('<b>').split('ENDBOLD').join('</b>').toString().split('END').join('')}`
        }else{
            setSearchParams({num: [qe.length-1]})
            if(!qe[currentQ]){
                setTimeout(() => {
                    window.location.reload()
                }, 10);
            }
        }

        // when the user is about to leave the leave then mark the question 
        return (()=> {
            console.log("component will unmount");
        })
    },[currentQ])




    const iWantToAns = (v,e) => {
        let newAns = {[currentQ]: {
            userAns:v,
            questionAsked:qe[currentQ].q,
            passed: v === qe[currentQ].ans
        }}
        let existingAnswers = {...JSON.parse(localStorage.getItem(title))}
        console.log(existingAnswers);
        console.log(newAns);
        let newData = {...existingAnswers,...newAns}
        localStorage.setItem(title, JSON.stringify(newData))//get the prev and update 
        setAnwser(v)
    }


    return (
    <div style={{padding: '30px 20%'}} className='btn'>
    <center>
        <img style={{width: '150px', objectFit: 'cover'}} src='data:image/png;base64,' alt='abia state' />
    </center>
        <h1>{title}</h1>
        <br/>

        { areYouSure ? <Confirmation areYouSureHandlerExit={areYouSureHandlerExit} /> : null }

       {currentQ < qe.length && <>
        <NumberIndicator number={currentQ} total={(qe.length-1)} />
        <br/>


        <div ref={qesRef} style={{margin: '10px'}}></div>
        <br/>
         
        <div className='ol'>
        {qe[currentQ].options && <ol type='a'>
            {qe[currentQ].options.map((v,i)=>(
                <li key={i}> 
                    <input type={'radio'} id={v} onClick={e=>iWantToAns(v)} name={qe[currentQ]} />&#160;{v}
                </li>
            ))}
        </ol>}
        </div>
        {currentQ>0 && <button onClick={prevQ}>Previous Question</button>}
        {(currentQ+1) <= (qe.length-1) && <button style={{float: 'right'}} onClick={nextQ}>Next Question</button>}

        <SubmitButton areYouSureHandler={areYouSureHandler} />


        </>}
    </div>
  )
}




